export default {
	data() {
		return {
			addressList:[]
		}
	},
	methods: {
		add(){
			uni.navigateTo({
				url:'../site/Site?type=add'
			})
		},
		change(item,index){
			let list = JSON.stringify(item)
			uni.navigateTo({
				url:'../site/Site?name='+list+'&type=change'
			})
		},
		back(){
			uni.navigateTo({
				url:'../../pages/personage/Personage'
			})
		}
	},
	onLoad: function(options){
		uni.showLoading({
		    title: '加载中'
		});
		var that = this
		uni.request({
			url:"http://ceshi3.dishait.cn/api/useraddresses/1",
			header:{
				"token":"a4c30e2a39426b875290fcbd91c16727b2b7e3c5"
			},
			success(res) {
				setTimeout(function () {
				    uni.hideLoading();
				}, 500);
				that.addressList = res.data.data
			}
		})
	},
	onShow() {
		this.onLoad();
	}
}
